.to-do-list-container {
    margin-top: 25px;
  }
  
  .to-do-list-item {
    margin-top: 10px;
    color: white;
    -webkit-transition: background-color 1s;
    transition: background-color 1s;
  }
  
  .to-do-list-item h4 {
    padding-left: 25px;
    padding-bottom: 5px;
  }
  
  .to-do-list-item:hover {
    background-color: #26a69a !important;
  }
  
  .complete-todo-item {
    float: right;
    right: 20px;
  }
  
  #todo-add-form {
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-name: slidein;
            animation-name: slidein;
  }
  
  #nothing-was-found {
    margin-top: 150px;
  }
  
  @-webkit-keyframes slidein {
    from {
      opacity: 0;
    }
  
    to {
      margin-left: 1;
    }
  }
  
  @keyframes slidein {
    from {
      opacity: 0;
    }
  
    to {
      margin-left: 1;
    }
  }
.social-signin {
    padding: 20px 40px 20px 40px;
    font-size: 30px;
    border-radius: 100px;
    text-align: center;
    text-decoration: none;
    background: #dc453a;
    color: white;
    -webkit-transition: opacity 0.5s;
    transition: opacity 0.5s;
  }
  
  .social-signin-container {
    margin-top: 15%;
  }
  
  .social-signin:hover {
    opacity: 0.9;
  }
  
  .social-signin-icon {
    padding-right: 15px;
  }
  
  #sign-in {
    margin-bottom: -20px;
  }
  
  #sign-in-header {
    margin-bottom: 40px;
  }
  
